.skills__point .skills__secondary-skill {
  transform: translateX(-450px);
}

.skills__point .skills__secondary-skill.active {
  transform: translateX(0px);
}

.skills__point:nth-child(1) .skills__secondary-skill {
  transition-delay: 0s;
}

.skills__point:nth-child(2) .skills__secondary-skill {
  transition-delay: 0.1s;
}

.skills__point:nth-child(3) .skills__secondary-skill {
  transition-delay: 0.2s;
}

.skills__point:nth-child(4) .skills__secondary-skill {
  transition-delay: 0.3s;
}

.skills__point:nth-child(5) .skills__secondary-skill {
  transition-delay: 0.4s;
}

.portfolio__point:nth-child(2) {
  opacity: calc((var(--scrollTop) - var(--scrollTopPortfolio) + var(--windowHeight) * 0.6) / (var(--windowHeight) * 0.3));
  transition: opacity 0.3s linear;
  will-change: opacity;
}

.portfolio__point:nth-child(3) {
  opacity: calc((var(--scrollTop) - var(--scrollTopPortfolio) + var(--windowHeight) * 0.3) / (var(--windowHeight) * 0.3));
  transition: opacity 0.3s linear;
  will-change: opacity;
}

.portfolio__point:nth-child(4) {
  opacity: calc((var(--scrollTop) - var(--scrollTopPortfolio) - var(--windowHeight) * 0) / (var(--windowHeight) * 0.3));
  transition: opacity 0.3s linear;
  will-change: opacity;
}

.portfolio__point:nth-child(5) {
  opacity: calc((var(--scrollTop) - var(--scrollTopPortfolio) - var(--windowHeight) * 0.3) / (var(--windowHeight) * 0.3));
  transition: opacity 0.3s linear;
  will-change: opacity;
}

.portfolio__point:nth-child(6) {
  opacity: calc((var(--scrollTop) - var(--scrollTopPortfolio) - var(--windowHeight) * 0.6) / (var(--windowHeight) * 0.3));
  transition: opacity 0.3s linear;
  will-change: opacity;
}
