@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800;900&display=swap');

* {
    box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h2, h3, p {
  margin: 0;
}

.container {
  max-width: 1040px;
  width: 100%;
  margin: 0 auto;
}

header {
  padding-top: 38px;
  padding-bottom: 30px;
  border-bottom: 1px solid #828282;
}

.header__sections-list {
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: space-between;
  padding: 0px;
  margin: 0px;
}

.header__sections-list li {
  list-style: none;
}

.header__section {
  display: inline-block;

  font-weight: 600;
  font-size: 18px;
  line-height: 22px;
  color: #828282;
  text-decoration: none;
  transition: opacity 0.3s linear;
}

.header__section-github {
  font-weight: 700;
  color: #000;
}

.header__section:hover {
  cursor: pointer;
  opacity: 0.7;
}

.header__section.active {
  color: #000;
}

.hero {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin-top: 50px;
  margin-bottom: 50px;
}

.hero-desc {
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  text-align: right;
  color: #070707;
}

h1 {
  margin: 0px;
  font-weight: 600;
  font-size: 36px;
  line-height: 44px;
  color: #070707;
}

.hero-image {
  min-height: 530px;
  width: 100%;
  margin-top: 50px;
  margin-bottom: 100px;
  background-image: url(../img/portfolio.jpg);
  background-size: cover;
  background-position: 100% 50%;
}

.main {
  width: 100%;
  background: #F6F6F6;

}

.main__conainer {
  position: relative;
  max-width: 1040px;
  width: 100%;
  margin: 0 auto;
  padding-left: 120px;
  padding-right: 20px;
}

.main__progress {
  display: flex;
  justify-content: center;
  position: absolute;
  width: 80px;
  left: 0px;
  top: 60px;
  bottom: 10px;
}

.main__line {
  width: 10px;
  height: 100%;
  background: rgba(145, 179, 242, 0.6);
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.25);
}

.progress__button {
  position: absolute;
  width: 60px;
  height: 60px;
  border: 0;
  border-radius: 40px;
  background: #91B3F2;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.25);
}

.about {
  width: 100%;
  padding-top: 68px;
  background: #F6F6F6;
}

.about__inner {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  max-width: 590px;
}

.about__inner-part {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 100%;
}

.about h2 {
  margin-bottom: 60px;
  font-weight: 700;
  font-size: 36px;
  line-height: 44px;
  text-align: center;
  color: #000000;
  opacity: calc((var(--scrollTop) - var(--scrollTopAbout) + var(--windowHeight) * 0.7) / (var(--windowHeight) * 0.2));
  transition: opacity 0.3s linear;
  will-change: opacity;
}

.about__p {
  max-width: 500px;
  margin: 0px;
  margin-bottom: 10px;
  font-weight: 500;
  font-size: 20px;
  line-height: 150%;
  color: #696969;
  opacity: calc((var(--scrollTop) - var(--scrollTopAbout) + var(--windowHeight) * 0.6) / (var(--windowHeight) * 0.2));
  transition: opacity 0.3s linear;
  will-change: opacity;
}

.about__p:last-of-type {
  margin-bottom: 30px;
}

.about__education-header {
  margin-bottom: 20px;
  font-weight: 700;
  font-size: 24px;
  line-height: 29px;
  text-align: center;
  color: #000000;
  opacity: calc((var(--scrollTop) - var(--scrollTopAbout) + var(--windowHeight) * 0.5) / (var(--windowHeight) * 0.2));
  transition: opacity 0.3s linear;
  will-change: opacity;
}

.about__education-small-header {
  margin: 0px;
  margin-bottom: 10px;
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  text-decoration-line: underline;
  color: #696969;
  opacity: calc((var(--scrollTop) - var(--scrollTopAbout) + var(--windowHeight) * 0.4) / (var(--windowHeight) * 0.2));
  transition: opacity 0.3s linear;
  will-change: opacity;
}

.about__education-p {
  margin-bottom: 10px;
  font-weight: 500;
  font-size: 20px;
  line-height: 150%;
  color: #696969;
  opacity: calc((var(--scrollTop) - var(--scrollTopAbout) + var(--windowHeight) * 0.3) / (var(--windowHeight) * 0.2));
  transition: opacity 0.3s linear;
  will-change: opacity;
}

.about__education-p:last-of-type {
  margin-bottom: 30px;
  opacity: calc((var(--scrollTop) - var(--scrollTopAbout) + var(--windowHeight) * 0.2) / (var(--windowHeight) * 0.2));
  transition: opacity 0.3s linear;
  will-change: opacity;
}

.about__experience-header {
  margin-bottom: 30px;
  font-weight: 700;
  font-size: 24px;
  line-height: 29px;
  text-align: center;
  color: #000000;
  opacity: calc((var(--scrollTop) - var(--scrollTopAbout) + var(--windowHeight) * 0.1) / (var(--windowHeight) * 0.2));
  transition: opacity 0.3s linear;
  will-change: opacity;
}

.about__experience-points {
  display: flex;
  flex-direction: column;
  margin-bottom: 30px;
}

.about__experience-point {
  display: flex;
  justify-content: space-between;
  width: 100%;
  gap: 50px;
  margin-bottom: 30px;
  opacity: calc((var(--scrollTop) - var(--scrollTopAbout) + var(--windowHeight) * 0) / (var(--windowHeight) * 0.2));
  transition: opacity 0.3s linear;
  will-change: opacity;
}

.about__experience-point:nth-child(2) {
  opacity: calc((var(--scrollTop) - var(--scrollTopAbout) - var(--windowHeight) * 0.1) / (var(--windowHeight) * 0.2));
  transition: opacity 0.3s linear;
  will-change: opacity;
}

.about__experience-point-left {
  width: 350px;
}

.about__experience-h4 {
  margin: 0px;
  margin-bottom: 20px;
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  text-decoration-line: underline;
  color: #696969;
}

.about__experience-p {
  font-weight: 500;
  font-size: 20px;
  line-height: 150%;
  color: #696969;
}

.about__experience-point-right {
  width: 190px;
  font-style: normal;
  font-weight: 600;
  font-size: 20px;
  line-height: 24px;
  color: #696969;
}

.skills__inner {
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    max-width: 590px;
    margin-bottom: 40px;
}

.skills h2 {
  margin-bottom: 60px;
  font-weight: 700;
  font-size: 36px;
  line-height: 44px;
  text-align: center;
  color: #000000;
}

.skills__points {
  display: flex;
  flex-direction: column;
}

.skills__point {
  position: relative;
  overflow: hidden;
  display: flex;
  gap: 20px;
  margin-bottom: 20px;
}

.skills__main-skill, .skills__secondary-skill {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100px;
  height: 100px;
  border-radius: 10px;
  border: 0;
  background: #DBEFFF;
  font-family: 'Montserrat';
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 150%;
  color: #000000;
}

.skills__main-skill {
  position: relative;
  z-index: 10;
}

.skills__secondary-skill {
  background: rgba(219, 239, 255, 0.6);
  color: rgba(0, 0, 0, 0.6);
  transition: transform 1s ease-out;
}

.skills__secondary-skill_long {
  width: 160px;
}

.portfolio {
  width: 100%;
  margin-bottom: 40px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.portfolio h2 {
  margin-bottom: 60px;
  font-weight: 700;
  font-size: 36px;
  line-height: 44px;
  text-align: center;
  color: #000000;
  opacity: calc((var(--scrollTop) - var(--scrollTopPortfolio) + var(--windowHeight) * 0.7) / (var(--windowHeight) * 0.2));
  transition: opacity 0.3s linear;
  will-change: opacity;
}

.portfolio__point {
  display: flex;
  width: 100%;
  margin-bottom: 20px;
  padding: 20px;
  box-shadow: -0.9rem 0.6rem 4.6rem 0 rgb(0 0 0 / 14%);
  background: white;
  text-decoration: none;
}

.portfolio__point-img {
  width: 50%;
}

.portfolio__point-desc {
  width: 50%;
  padding-left: 40px;
}

.portfolio__point-desc h3 {
  margin-bottom: 15px;
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  color: #696969;
}

.portfolio__point-desc span {
  display: inline-block;
  margin-bottom: 15px;
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  text-align: center;
  text-decoration: none;
  color: #696969;
}

.portfolio__point-tag {
  margin-right: 10px;
  margin-bottom: 10px;
  padding: 8px 11px;
  border: 0;
  background: #b3dbe8;
  border-radius: 10px;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: #696969;
  letter-spacing: 1px;
}

.contacts {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding-bottom: 10px;
}

.contacts h2 {
  margin: 0;
  margin-bottom: 30px;
  font-weight: 700;
  font-size: 36px;
  line-height: 44px;
  text-align: center;
  color: #000000;
}

.contacts__desc {
  max-width: 512px;
  margin-bottom: 30px;
  font-weight: 600;
  font-size: 20px;
  line-height: 150%;
  color: #696969;
}

.contacts__icons {
  display: flex;
  margin-bottom: 30px;
  gap: 30px;
}

.contacts__mail {
  margin-bottom: 20px;
  font-weight: 600;
  font-size: 20px;
  line-height: 24px;
  text-align: center;
  color: #828282;
}

.contacts__kwork {
  margin-bottom: 0px;
  font-weight: 600;
  font-size: 20px;
  line-height: 24px;
  text-align: center;
  text-decoration: none;
  color: #828282;
}

.contacts__date {
  width: 100%;
  background: #F6F6F6;
  font-weight: 400;
  font-size: 32px;
  line-height: 39px;
  text-align: center;
  color: #000000;
}

.burger {
  display: none;
  z-index: 20;
}

.burger-menu {
  display: none;
}

/* background colors */

.b-green {
    background: #90EE90;
}

.b-blue {
    background: #3F8FD2;
}

.b-orange {
    background: #FFBF40;
}

/* Loaders */

.loader-wrapper {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.loader{
  width: 100px;
  height: 100px;
  border-radius: 100%;
  position: relative;
  margin: 0 auto;
}

#loader-2 span{
  display: inline-block;
  width: 20px;
  height: 20px;
  border-radius: 100%;
  background-color: #3498db;
  margin: 35px 5px;
}

#loader-2 span:nth-child(1){
  animation: bounce 1s ease-in-out infinite;
}

#loader-2 span:nth-child(2){
  animation: bounce 1s ease-in-out 0.33s infinite;
}

#loader-2 span:nth-child(3){
  animation: bounce 1s ease-in-out 0.66s infinite;
}

@keyframes bounce{
  0%, 75%, 100%{
    -webkit-transform: translateY(0);
    -ms-transform: translateY(0);
    -o-transform: translateY(0);
    transform: translateY(0);
  }

  25%{
    -webkit-transform: translateY(-20px);
    -ms-transform: translateY(-20px);
    -o-transform: translateY(-20px);
    transform: translateY(-20px);
  }
}
