@media (max-width: 1100px) {
  .container {
    padding-left: 40px;
    padding-right: 40px;
  }
}

@media (max-width: 1024px) {
  .header__section {
    font-size: 16px;
  }

  h1 {
    font-size: 28px;
  }

  .hero {
    margin-top: 35px;
    margin-bottom: 35px;
  }

  .hero-image {
    margin-top: 35px;
  }

  .hero-desc {
    font-size: 16px;
  }

  .hero-image {
    min-height: 400px;
    margin-bottom: 50px;
  }


  .about h2, .skills h2, .portfolio h2 {
    margin-bottom: 35px;
    font-size: 32px;
  }

  .about p {
    font-size: 16px;
  }

  .about__experience-point-right {
    font-size: 16px;
  }

  .skills__list li, .skills__list h3 {
    font-size: 20px;
  }

  .portfolio-swiper__slide {
    margin-bottom: 35px;
  }

  .swiper-slide h3, .swiper-slide a {
    font-size: 16px;
  }

  .swiper-slide {
    padding: 0px 80px 120px 80px;
  }

  .contacts {
    padding-top: 50px;
  }

  .contacts h2 {
    font-size: 24px;
  }

  .contacts__desc, .contacts__mail, .contacts__kwork {
    font-size: 16px;
  }

  .portfolio__point-desc h3, .portfolio__point-desc span {
    font-size: 16px;
  }

  .portfolio__point-tag {
    font-size: 12px;
    padding: 4px 6px;
  }
}

@media (max-width: 768px) {
  .about__education-small-header, .about__experience-h4, .about__experience-h4 {
    font-size: 16px;
  }

  .about__experience-point {
    flex-wrap: wrap;
    gap: 10px;
    margin-bottom: 50px;
  }

  .about__experience-point-left {
    width: 100%;
  }

  .skills__point {
    gap: 10px;
    margin-bottom: 10px;
  }

  .skills__main-skill, .skills__secondary-skill {
    width: 60px;
    height: 60px;
    border-radius: 5px;
    font-size: 12px;
  }

  .skills__secondary-skill_long {
    width: 120px;
  }
}

@media (max-width: 600px) {
  .container {
    padding-left: 20px;
    padding-right: 20px;
  }

  header {
    display: none;
  }

  .hero {
    flex-direction: column;
    align-items: flex-start;
  }

  .hero-desc {
    text-align: left;
  }

  .hero {
    position: relative;
  }

  .main__progress {
    display: none;
  }

  .main__conainer {
    padding-left: 20px;
  }

  .skills__list {
    flex-direction: column;
  }

  .swiper-button-prev, .swiper-button-next {
    display: none !important;
  }

  .swiper-slide {
    height: 400px !important;
    padding: 0px 0px 160px 0px;
  }

  .burger {
    position: absolute;
    display: inline-block;
    background: #fff;
    padding: 0px;
    border: 0;
    right: 20px;
    top: 40px;
  }

  .burger-menu {
    position: fixed;
    display: inline-block;
    z-index: 10;
    top: 0;
    left: -100vw;
    width: 100vw;
    height: 100%;
    background: #F6F6F6;
    padding-top: 120px;
    transition: left 0.5s linear;
  }

  .burger-menu.active {
    left: 0px;
  }

  .burger-close {
    position: absolute;
    z-index: 20;
    top: 40px;
    right: 40px;
    background: transparent;
    padding: 0px;
    border: 0;
  }

  .header__sections-list {
    flex-direction: column;
  }

  .header__section {
    font-size: 24px;
    margin-bottom: 30px;
  }

  .about h2, .skills h2, .portfolio h2 {
    font-size: 24px;
  }

  .about__education-header, .about__experience-header {
    font-size: 16px;
  }

  .main__conainer p, .about__experience-point-right {
    font-size: 14px;
  }

  .portfolio__point {
    flex-direction: column;
  }

  .portfolio__point-img {
    width: 100%;
  }

  .portfolio__point-desc {

    width: 100%;
    margin-top: 20px;
    padding-left: 0px;
  }

  @media (max-width: 400px) {
    .skills__point {
      gap: 4px;
      margin-bottom: 4px;
    }

    .skills__main-skill, .skills__secondary-skill {
      width: 50px;
      height: 50px;
      border-radius: 5px;
      font-size: 9px;
    }

    .skills__secondary-skill_long {
      width: 90px;
    }
  }
}
